.Container {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  left: 0;
  top: 0;
}
.App-Text {
  font-size: 50px;
}
.Button {
  background-color: tomato;
}
button {
  border: none;
  background-color: transparent;
  font-size: 20px;
  padding: 20px;
  border-radius: 5px;
  font-weight: 900;
}
button:hover {
  cursor: pointer;
  box-shadow: 2px 3px 4px black;
}
